<template>
  <div>
    <mt-header :title="msg">
      <router-link to="/" slot="left">
        <mt-button icon="back">返回</mt-button>
      </router-link>
      <!-- <mt-button @click="onOperate" icon="more" slot="right"></mt-button> -->
    </mt-header>
    活动activity_id为：{{$route.params.activity_id}}
    <!-- <mt-actionsheet :actions="sheetactions" v-model="sheetVisible"></mt-actionsheet> -->
    <template v-for="member in members">
      <mt-cell :title="member.name" :label="member.ecnuId"></mt-cell>
    </template>

    <!-- <button @click="onCancel" class="mint-button mint-button--primary mint-button--large">取消报名</button> -->
  </div>
</template>

<script>
import { getActivityDetail, getAcceptuser } from "../../request/api"; // 导入我们的api接口
export default {
  name: "DetailPage",
  data() {
    return {
      msg: "报名名单",
      item: {},
      members: {},
      sheetVisible: false,
      sheetactions: [
        {
          name: "查看报名人员",
          method: this.getmemberlist // 调用methods中的函数
        }
      ]
    };
  },
  created: function() {
    let openId = this.$cookies.get("openId");
    if (openId === null) {
      console.log("openid为空");
      window.location = "/";
    }
    let params = { id: this.$route.params.activity_id };
    console.log(params);
    getActivityDetail(params).then(res => {
      this.item = res.data;
      console.log(this.item);
    });
    params.a_id = params.id;
    getAcceptuser(params).then(res => {
      console.log(res);
      this.members = res.data;
    });
  },
  methods: {
    onCancel: function() {},
    onOperate: function() {
      this.sheetVisible = true;
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1,
h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}

/* a {
  color: #42b983;
} */
</style>



